<template>
  <v-container>
    <v-row>
      <!-- Columna Izquierda -->
      <v-col sm="6" md="6" lg="6">
        <v-form>
          <h1>Excepciones comunes</h1>

          <!-- Checkbox Group para las Excepciones -->
          <div class="checkbox-container">
            <v-checkbox v-model="excepciones" :value="'Falta de capacidad'" label="Falta de capacidad"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Falsedad del ttulo'" label="Falsedad del ttulo"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Falta de requisitos'" label="Falta de requisitos"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Exceso de avalo'" label="Exceso de avalo"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Pago de la deuda'" label="Pago de la deuda"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Concesin de esperas'" label="Concesin de esperas"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Nulidad de la obligacin'" label="Nulidad de la obligacin"></v-checkbox>
          </div>
          <v-divider></v-divider>
          <div class="checkbox-container2">
            <v-checkbox v-model="excepciones" :value="'Incompetencia'" label="Incompetencia"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Ineptitud del libelo'" label="Ineptitud del libelo"></v-checkbox>
            <v-checkbox v-model="excepciones" :value="'Prescripcin'" label="Prescripcin"></v-checkbox>
          </div>
        </v-form>
        <v-btn :to="'/'">Volver</v-btn> 
        <v-btn @click="clearFields">Borrar todo</v-btn>
      </v-col>

      <!-- Columna Derecha -->
      <v-col sm="6" md="6" lg="6">
        <h2>Resolucin</h2>
        <div ref="ResolucionText" class="resolucion-texto">
          <p><strong>Resolviendo presentacin de fecha XXXX de XXXXX de XXXX, de folio XXXX:</strong><br></p>
          <p>Tngase por evacuado el traslado conferido por resolucin de folio XXXX. Estese a lo que se resolver. <br></p>
          <p><strong>VISTOS:</strong><br></p>
          <p>Se declaran admisibles las excepciones deducidas, y se reciben a prueba por el trmino legal, fijndose como puntos sobre los cuales debern recaer, los siguientes;<br></p>
          <!-- Lista numerada para excepciones del primer grupo -->
          <ol v-if="excepcionesFiltradas.length > 0">
            <li v-for="(excepcion, index) in excepcionesFiltradas" :key="index">
              {{ generarTexto(excepcion) }}
            </li>
          </ol>

          <!-- Prrafos para excepciones del segundo grupo -->
          <div v-if="excepcionesNoNumeradas.length > 0">
            <p v-for="(excepcion, index) in excepcionesNoNumeradas" :key="index">
              {{ generarTexto(excepcion) }}
            </p>
          </div>

          <br><p>En caso que las partes ofrezcan prueba testimonial y/o confesional dentro del plazo legal, esta se rendir en forma presencial en el tribunal los ltimos tres das del probatorio a las 9:30 horas, principiando con el demandante.</p> <br>
          <p><strong>Notifquese por cdula.</strong></p>
        </div>
        <div class="button">
          <v-tooltip v-model="tooltipVisible" bottom>
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                @click="copyToClipboard"
              >
                Copiar
              </v-btn>
            </template>
            <span>Texto copiado al portapapeles</span>
          </v-tooltip>
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: 'HelloWorld',

  data() {
    return {
      traslado: '',
      excepciones: [], // Lista de excepciones seleccionadas
      tooltipVisible: false, // Controla la visibilidad del tooltip
    };
  },
  computed: {
    excepcionesFiltradas() {
      // Filtra las excepciones que estn en el grupo "checkbox-container"
      const grupo1 = ['Falta de capacidad', 'Falsedad del ttulo', 'Falta de requisitos', 'Exceso de avalo', 'Pago de la deuda', 'Concesin de esperas', 'Nulidad de la obligacin'];
      return this.excepciones.filter(ex => grupo1.includes(ex));
    },
    excepcionesNoNumeradas() {
      // Filtra las excepciones que estn en el grupo "checkbox-container2"
      const grupo2 = ['Incompetencia', 'Ineptitud del libelo', 'Prescripcin'];
      return this.excepciones.filter(ex => grupo2.includes(ex));
    }
  },
  methods: {
    generarTexto(excepcion) {
      if (excepcion === 'Incompetencia') {
        return 'Respecto a la excepcin de incompetencia, se omite su recepcin a prueba por tratarse de un punto de derecho.';
      } else if (excepcion === 'Falta de capacidad') {
        return 'Efectividad de existir la falta de capacidad del demandante o de la personera o representacin legal de la persona que comparece en su nombre para actuar en autos.';
      } else if (excepcion === 'Ineptitud del libelo') {
        return 'Respecto a la excepcin de ineptitud del libelo, atendida su naturaleza, se omite su recepcin a prueba.';
      } else if (excepcion === 'Falsedad del ttulo') {
        return 'Efectividad que el ttulo que se hace valer en juicio es falso.';
      } else if (excepcion === 'Falta de requisitos') {
        return 'Efectividad que el ttulo que se hace valer en juicio carece de alguno de los requisitos o condiciones que establece la ley para que tenga mrito ejecutivo.';
      } else if (excepcion === 'Exceso de avalo') {
        return 'Efectividad que en la obligacin pretendida existira un error de clculo que constituira un exceso de avalo de la ejecucin del presente juicio.';
      } else if (excepcion === 'Pago de la deuda') {
        return 'Efectividad que las demandadas pagaron total o parcialmente la obligacin que se persigue; fechas y montos.';
      } else if (excepcion === 'Concesin de esperas') {
        return 'Efectividad de haber concedido la ejecutante esperas o prrrogas del plazo a la ejecutada.';
      } else if (excepcion === 'Novacin') {
        return 'Texto para Novacin';
      } else if (excepcion === 'Nulidad de la obligacin') {
        return 'Efectividad que la obligacin que se persigue en estos autos es nula.';
      } else if (excepcion === 'Prdida de la cosa') {
        return 'Texto para Prdida de la cosa';
      } else if (excepcion === 'Prescripcin') {
        return 'Respecto a la excepcin de prescripcin, atendida su naturaleza, se omite su recepcin a prueba.';
      }
      return '';
    },
    copyToClipboard() {
      const range = document.createRange();
      range.selectNode(this.$refs.ResolucionText);
      window.getSelection().removeAllRanges(); // Limpia la seleccin previa
      window.getSelection().addRange(range); // Selecciona el contenido
      try {
        document.execCommand('copy'); // Copia el contenido seleccionado
        this.tooltipVisible = true;
        setTimeout(() => {
          this.tooltipVisible = false;
        }, 2000);
      } catch (err) {
        console.error('No se pudo copiar el texto', err);
      }
      window.getSelection().removeAllRanges(); // Limpia la seleccin
    },
    // Mtodo para limpiar todos los campos
    clearFields() {
      this.traslado = '';
      this.excepciones = [];
    }
  }
}
</script>

<style>
.checkbox-container {
  display: flex;
  flex-wrap: wrap;
}

.checkbox-container > * {
  margin-right: 10px;
}

.checkbox-container2 {
  display: flex;
  flex-wrap: wrap;
}

.button {
  margin-top: 3%;
}

.resolucion-texto {
  font-family: 'Batang', serif;
  font-size: 12px;
  text-align: justify;
}
</style>
